import { Component } from 'react';

class Footer extends Component {
	render() {
		const footer = this.props.footer;

		return (
			<div style={{ borderTop: "1px solid #CCCCCC", marginTop: "4rem", textAlign: "center" }}>
				<p>Created by <a href="https://github.com/Lee-Minhoon/Lee-Minhoon.github.io" target="_blank" rel="noopener noreferrer">React</a></p>
				<p>Last Updated at {footer}</p>
			</div>
		);
	}
}

export default Footer;